// CONTROLLER
// =============================================================================
class EditButtonController {
	/**
	 * Fires when the controller is finished initializing.
	 *
	 * @method $onInit
	 */
	$onInit() {
		// controller name
		this.name = "editButton";
		// initialize editing
		this.editing = false;
	}
}


// EXPORT
// =============================================================================
export default EditButtonController;



